<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element section">
        <toggle
          ref="Toggle"
          label-info="Enable Pardot Integration"
          :statuses="toggleStatuses"
          :is-new-opened="isNewOpened"
          :disabled="isSaving || !isEditable"
          @toggle="onToggle"
          @over="showTooltip($event)"
          @out="hideTooltip"
        ></toggle>

        <div
          class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left tooltip"
          role="tooltip"
          :style="{position: 'fixed', left: infoTooltipPosition.left, top: infoTooltipPosition.top}"
          v-if="showInfo"
        >
          <div class="slds-popover__body">{{ tooltipContent }}</div>
        </div>
      </div>
    </div>
  </right-page>
</template>

<script>
import { PardotService } from '../service'

export default {
  name: 'PardotSettings',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Pardot Settings'
      },
      isPardotEnabled: false,
      toggleStatuses: ['Enabled', 'Disabled'],
      isNewOpened: false,
      isEditable: true,
      showInfo: false,
      infoTooltipPosition: {
        left: 0,
        top: 0
      },
      tooltipContent: '',
      isSaving: false
    }
  },
  created () {
    this.fetchPardotSettings()
  },
  methods: {
    onToggle (status) {
      if (!this.isSaving && this.isEditable) {
        this.isPardotEnabled = status
        this.savePardotSettings()
      }
    },
    async fetchPardotSettings () {
      this.$bus.emit('show-loading')
      await this.$try(async () => {
        const { isPardotEnabled, isEditable, message } = await PardotService.getSettings()
        this.isPardotEnabled = isPardotEnabled
        this.isEditable = isEditable
        this.tooltipContent = message
        this.isNewOpened = this.isPardotEnabled
      })
      this.$bus.emit('hide-loading')
    },
    async savePardotSettings () {
      this.isSaving = true
      try {
        await PardotService.saveSettings(this.isPardotEnabled)
      } catch (error) {
        if (error.name === 'RemoteAPIError') {
          this.$refs.Toggle.reverse()
        }
        this.$handleError(error)
      } finally {
        this.isSaving = false
      }
    },
    showTooltip (event) {
      if (this.isEditable) {
        return
      }

      this.showInfo = true
      this.$nextTick(() => {
        const infoIconPosition = event.currentTarget.getBoundingClientRect()
        const tooltipEle = document.querySelector('.tooltip')
        // 16 is the tooltip left border
        // 12 is the arrow height
        this.infoTooltipPosition.left = `${infoIconPosition.left - 16}px`
        this.infoTooltipPosition.top = `${infoIconPosition.top - tooltipEle.clientHeight - 12}px`
      })
    },
    hideTooltip () {
      this.showInfo = false
    }
  }
}
</script>

<style scoped lang="scss">
  .section {
    padding: 20px 30px;
  }
</style>
